/*

* Stylesheet
* 
*/
@import url("https://fonts.googleapis.com/css?family=Nunito+Sans:300,400,600,700|Playfair+Display:400,700&display=swap");
p {
  font-size: 1.3rem;
  letter-spacing: 1px;
  font-weight: 300;
}
a {
  color: #008cdd;
}
.no-margin {
  margin: 0 !important;
}
.no-padding {
  padding: 0 !important;
}
.hide,
.hidden {
  display: none !important;
}
.show {
  display: block !important;
}
.invisible {
  visibility: hidden;
}
.clearfix {
  clear: fix-legacy;
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}
html {
  font-family: 'Nunito Sans', sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background: transparent;
  text-decoration: none;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
mark {
  background: #f1c40f;
  color: $monochrome-8;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
ul,
ol,
dl {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
hr {
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  -moz-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -moz-appearance: none;
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid $monochrome-2;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html font-size $text-size {
  font-family: 'Nunito Sans', sans-serif;
}
body {
  background-color: #f8f8f8;
}
.container {
  margin: 0 auto;
  max-width: 100%;
  padding: 2rem 30px;
  width: 1280px;
}
.main-page {
  text-align: center;
}
section {
  margin-bottom: 90px;
}
section:last-child {
  margin-bottom: 0;
}
.section-title {
  font-family: 'Playfair Display', serif;
  color: #151b1e;
  letter-spacing: 1px;
  font-size: 1.8rem;
}
.section-text {
  font-size: 1.2rem;
  line-height: 2rem;
  color: #3e515b;
  font-weight: 300;
}
.grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: -20px;
  margin-right: -20px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.grid-item {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 50%;
          flex: 0 0 50%;
  max-width: 50%;
  padding: 0px 20px 20px 20px;
}
@media only screen and (max-width: 767.98px) {
  .grid-item {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
            flex: 0 0 100%;
    max-width: 100%;
  }
}
.portfolio-item img {
  width: 100%;
}
.portfolio-item p {
  font-size: 1.1rem;
  margin-top: 8px;
  color: #3e515b;
  line-height: 1.8rem;
}
.porfolio {
  margin-top: 30px;
}
.send-mail {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  background: #32295d;
  border: none;
  padding: 0;
  width: 60px;
  height: 60px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  position: relative;
  border-radius: 100%;
  -webkit-animation: ripple 0.7s linear infinite;
          animation: ripple 0.7s linear infinite;
}
.send-mail img {
  width: 25px;
  height: 25px;
}
@-webkit-keyframes ripple {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(37,37,37,0.3), 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3);
            box-shadow: 0 0 0 0 rgba(37,37,37,0.3), 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3), 0 0 0 1.25em rgba(37,37,37,0);
            box-shadow: 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3), 0 0 0 1.25em rgba(37,37,37,0);
  }
}
@keyframes ripple {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(37,37,37,0.3), 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3);
            box-shadow: 0 0 0 0 rgba(37,37,37,0.3), 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3), 0 0 0 1.25em rgba(37,37,37,0);
            box-shadow: 0 0 0 0.25em rgba(37,37,37,0.3), 0 0 0 0.75em rgba(37,37,37,0.3), 0 0 0 1.25em rgba(37,37,37,0);
  }
}
footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: right;
}
footer .container {
  padding: 30px;
}
header .container {
  padding-bottom: 0;
}
.profile {
  display: block;
  text-align: center;
}
.profile__img {
  width: 200px;
  border-radius: 4px;
  -webkit-box-shadow: 0 30px 40px 0 rgba(212,217,232,0.8);
          box-shadow: 0 30px 40px 0 rgba(212,217,232,0.8);
}
@media only screen and (max-width: 767.98px) {
  .profile__img {
    width: 150px;
  }
}
.profile__content {
  margin-top: 30px;
}
.profile__content-title {
  font-family: 'Playfair Display', serif;
  color: #151b1e;
  letter-spacing: 1px;
  margin-top: 0;
  margin-bottom: 6px;
  font-size: 2.2rem;
}
@media only screen and (max-width: 767.98px) {
  .profile__content-title {
    font-size: 1.8rem;
  }
}
.profile__content p {
  color: #777;
  margin-top: 0;
  margin-bottom: 22px;
}
.profile__content-social {
  margin-top: 0;
}
.profile__content-social li {
  display: inline-block;
}
.profile__content-social li:not(:last-child) {
  padding-right: 20px;
}
.profile__content-social li a img {
  height: 26px;
}
